import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ProjectService } from '../shared/project.service';
import { TagService } from '../shared/tag.service';
import { Tag } from '../shared/tag.model';

@Component({
  selector: 'app-tag-projects',
  templateUrl: './tag-projects.page.html',
  styleUrls: ['./tag-projects.page.scss'],
})
export class TagProjectsPage implements OnInit {

  private id: string;
  public tag: Tag;
  constructor(
    private route: ActivatedRoute,
    public projectService: ProjectService,
    public tagService: TagService
  ) { }

  ngOnInit() {
    this.id = this.route.snapshot.paramMap.get('id');
    console.log(this.id);
    this.projectService.getAllProjectsByTag(this.id);
    this.tagService.getTag(this.id).subscribe(data =>{
      this.tag = data;
      console.log(this.tag);
    });
  }

}
